import {tasksReducer} from '../features/TodolistsList/tasks-reducer';
import {todolistsReducer} from '../features/TodolistsList/todolists-reducer';
import {applyMiddleware, combineReducers, createStore} from 'redux'
import thunkMiddleware from 'redux-thunk'
import {appReducer} from "./app-reducer";
import {TypedUseSelectorHook, useSelector} from "react-redux";

// –æ–±—ä–µ–¥–∏–Ω—è—è reducer-—ã —Å –ø–æ–º–æ—â—å—é combineReducers,
// –º—ã –∑–∞–¥–∞—ë–º —Å—Ç—Ä—É–∫—Ç—É—Ä—É –Ω–∞—à–µ–≥–æ –µ–¥–∏–Ω—Å—Ç–≤–µ–Ω–Ω–æ–≥–æ –æ–±—ä–µ–∫—Ç–∞-—Å–æ—Å—Ç–æ—è–Ω–∏—è
const rootReducer = combineReducers({
    tasks: tasksReducer,
    todolists: todolistsReducer,
    app: appReducer
})
// –Ω–µ–ø–æ—Å—Ä–µ–¥—Å—Ç–≤–µ–Ω–Ω–æ —Å–æ–∑–¥–∞—ë–º store
export const store = createStore(rootReducer, applyMiddleware(thunkMiddleware));
// –æ–ø—Ä–µ–¥–µ–ª–∏—Ç—å –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ —Ç–∏–ø –≤—Å–µ–≥–æ –æ–±—ä–µ–∫—Ç–∞ —Å–æ—Å—Ç–æ—è–Ω–∏—è
export type AppRootStateType = ReturnType<typeof rootReducer>

export const useAppSelector: TypedUseSelectorHook<AppRootStateType> = useSelector

// –∞ —ç—Ç–æ, —á—Ç–æ–±—ã –º–æ–∂–Ω–æ –±—ã–ª–æ –≤ –∫–æ–Ω—Å–æ–ª–∏ –±—Ä–∞—É–∑–µ—Ä–∞ –æ–±—Ä–∞—â–∞—Ç—å—Å—è –∫ store –≤ –ª—é–±–æ–π –º–æ–º–µ–Ω—Ç
// @ts-ignore
window.store = store;
